@import url("https://fonts.googleapis.com/css2?family=Playfair+Display:wght@500&family=Raleway:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap");

body {
  font-family: "Playfair Display", serif !important;
}

.App {
  min-height: 100vh;
  display: flex;
  background: url("https://raw.githubusercontent.com/piyush-eon/mern-chat-app/master/frontend/src/background.png");
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
  color: #000;
}

.messages {
  display: flex;
  flex-direction: column;
  overflow-y: scroll;
  scrollbar-width: none;
}
